* {
  list-style: none;
  margin: 0;
  padding: 0;
}
.area {
  
  position: relative;
  max-width: 1600px;
  width: 90vw;
  overflow: hidden;
  margin: 80px auto 0 auto;
  border-radius: 30px;
  height: 250px;
}

.area .slider {
  display: flex;

  width: 10800px;
  height: 250px;
}

.slider_list {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 90vw;
  height: 250px;
}

.listbox {
  position: absolute;
  top: 300px;
  left: calc(50%);
  transform: translate(-50%, 0);
  max-width: 1000px;
  width: 80vw;
  height: 80px;
  background: white;
  box-shadow: 1px 1px 5px 1px rgb(219, 219, 219);
  border-radius: 15px;
}

.listbox ul {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.listbox li {
  border-left: 1px solid black;
  flex: 0.25;
  display: flex;
  justify-content: center;
}
.listbox li:nth-of-type(1) {
  border: none;
}
.listbox .listtext {
  text-decoration: none;
  color: black;
  display: flex;
  font-size: 1.1rem;
  font-weight: 700;
}
